<template lang="ko">
	<div class="detail-container">
		<div class="date">{{study.date}}</div>
		<div class="title">{{study.title}}</div>
		<ul class="detail">
			<li v-for="detail in study.details">{{detail}}</li>
		</ul>
		<div v-if="study.link != underline" >
			<a :href="study.link" class="link" target="blank">github</a><img src="@/assets/arrow.png">
		</div>
	</div>
</template>

<script>
export default {
  props: {
    study: {
      date: "",
      title: "",
      details: [],
      link: "",
    },
  },
};
</script>

