  background-color: #f6f8fa;
  padding: 20px;
  border-radius: 12px;
}
.detail-container:not(:last-child) {
  /* margin-bottom: 50px; */
  margin-bottom: 30px;
}
.date {
  font-family: AppleSDGothicNeoR;
  opacity: 80%;
  font-size: 15px;
}
.title {
  font-family: AppleSDGothicNeoSB;
  font-size: 19px;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.detail {
  font-family: AppleSDGothicNeoL;
