import Vue from "vue";
import VueRouter from "vue-router";
import homeView from "@/views/HomeView";
import aboutView from "@/views/AboutView";
import projectView from "@/views/ProjectView";
import techView from "@/views/TechView";
import naverWebtoon from "@/components/ProjectDetail/NaverWebtoon";
import attiProject from "@/components/ProjectDetail/AttiProject";
import happyHouse from "@/components/ProjectDetail/HappyHouse";

Vue.use(VueRouter);

const scrollToTop = (to, from, next) => {
  window.scrollTo(0, 0);
  next();
};

const routes = [
  {
    path: "/",
    name: "homeView",
    component: homeView,
  },
  {
    path: "/about",
    name: "aboutView",
    component: aboutView,
    beforeEnter: scrollToTop,
  },
  {
    path: "/project",
    name: "projectView",
    component: projectView,
    beforeEnter: scrollToTop,
  },
  {
    path: "/project/naver-webtoon",
    name: "naverWebtoon",
    component: naverWebtoon,
    beforeEnter: scrollToTop,
  },
  {
    path: "/project/atti",
    name: "attiProject",
    component: attiProject,
    beforeEnter: scrollToTop,
  },
  {
    path: "/project/happy-house",
    name: "happyHouse",
    component: happyHouse,
    beforeEnter: scrollToTop,
  },
  {
    path: "/tech",
    name: "techView",
    component: techView,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
