<template lang="ko">
	<div class="container">
		<div class="thumbnail-container">
			<img :src="require(`@/assets/${thumbnail.currentSrc}`)" class="thumbnail" @mouseover="changeToGif(thumbnail)" @mouseout="changeToStatic(thumbnail)">
		</div>
    <div class="guide">(상단 이미지 위에 마우스를 올리면 gif가 재생됩니다.)</div>
		<div class="title-container">
			<span class="title">{{title}}</span>
		</div>
		<div class="summary">
		<a href="https://www.data.go.kr/" target="blank">공공데이터포털 API</a>와 <a href="https://developers.kakao.com/" target="blank">KAKAO Map API</a>를 활용한 아파트 매매 정보 제공 웹사이트입니다.
		</div>
		<div class="line"></div>

    <div class="index-content-container">
      <div class="content-container">
        <!-- Overview -->
        <div class="block-container">
          <div class="sub-title">OverView</div>
          <ul class="content">
            <li>팀 구성: Full Stack (2)</li>
            <li>역할: 디자인 - 50%, FE - 50%, BE - 50%, README 작성 - 100%</li>
            <li>제작 기간: 2022.05.19 ~ 2022.05.26</li>
          </ul>
        </div>

        <!-- Teck Stack -->
        <div class="block-container">
          <div class="sub-title">Tech Stack</div>
          <ul class="content tech-stack-list">
            <li><span class="domain-name">FE</span> <span>Vue2</span> <span>Vuex</span> <span>axios</span> <span>Bootstrap-vue</span></li>
            <li><span class="domain-name">BE</span> <span>SpringBoot</span> <span>MyBatis</span> <span>Maven</span> <span>Mysql</span> <span>Swagger</span></li>
            <li><span class="domain-name">Tool</span> <span>Eclipse</span> <span>VSCode</span> <span>Figma</span><span>AdobeXD</span><span>Postman</span></li>
          </ul>
        </div>

        <!-- Features -->
        <div class="block-container">
          <div class="sub-title">Features✨</div>
          <div class="guide">(구현을 담당한 주요 기능들을 간추려 작성하였습니다. 이미지 위에 마우스를 올리면 gif가 재생됩니다.)</div>
          <div>
            <div v-for="feature in features" class="feature">
              <div class="feature-name">{{feature.title}}</div>
              <img :src="require(`@/assets/happyhouse/${feature.currentSrc}`)" class="gif" @mouseover="changeToGif(feature)" @mouseout="changeToStatic(feature)">
              <ul class="description">
                <li v-for="description in feature.descriptions">{{description}}</li>
              </ul>
            </div>

            <div class="feature-name">그 외 기능은 Github에 세부적으로 작성되어 있습니다.</div>
          </div>
        </div>

        <!-- Github --->
        <div class="block-container">
          <div class="sub-title"><a href="https://github.com/joohee56/Find-Home" class="link" target="blank">Github</a><img src="@/assets/arrow.png"></div>
        </div>
      </div>

      <IndexComp></IndexComp>
    </div>

    <!-- 위아래 이동 버튼 -->
    <div class="move-control-btn">
      <button class="up" @click="moveToTop">▴</button>
      <button @click="moveToBottom">▾</button>
    </div>
	</div>
</template>

<script>
import IndexComp from "./IndexComp.vue";

export default {
  data() {
    return {
      thumbnail: {
        currentSrc: "thumbnail_happyhouse.png",
        static: "thumbnail_happyhouse.png",
        gif: "intro_happyhouse.gif",
      },
      title: "HappyHouse",
      features: [
        {
          title: "법정동을 통한 아파트 매매 검색",
          currentSrc: "searchApt.png",
          static: "searchApt.png",
          gif: "searchApt.gif",
          descriptions: [
            "시, 군, 동을 선택하여 해당 지역 근처의 아파트 매매 내역 조회",
            "시,군,동 데이터, 아파트 매매 내역 데이터 : 공공데이터포털 API",
            "KAKAO API를 사용하여 지도 및 위도와 경도를 마커와 윈도우로 표시",
          ],
        },
        {
          title: "조회 내역 가격 낮은/높은 순 정렬",
          currentSrc: "sorting.png",
          static: "sorting.png",
          gif: "sorting.png",
          descriptions: [
            "아파트 매매 내역 결과를 가격 낮은 순 또는 높은 순서대로 정렬",
            "Merge Sort 알고리즘 사용",
          ],
        },
        {
          title: "아파트 매매 상세 조회",
          currentSrc: "detailSearch.png",
          static: "detailSearch.png",
          gif: "detailSearch.gif",
          descriptions: [
            "지도의 마커 클릭 시 아파트 매매 상세 내역 조회",
            "공공 데이터 포털의 상권 정보 API를 사용하여 주변 상권(은행, 약국, 마트, 카페, 편의점) 위치 제공",
          ],
        },
        {
          title: "지역 안전 지수 차트",
          currentSrc: "safetyIndex.jpg",
          static: "safetyIndex.jpg",
          gif: "safetyIndex.jpg",
          descriptions: [
            "각 지역의 안전지수를 교통, 화재, 범죄, 자연재해, 생활안전, 감염병의 등급의 평균 차트를 통해 제공",
            "chart.js 라이브러리 사용",
          ],
        },
      ],
    };
  },
  components: { IndexComp },
  methods: {
    changeToGif(img) {
      img.currentSrc = img.gif;
    },
    changeToStatic(img) {
      img.currentSrc = img.static;
    },
    moveToTop() {
      window.scrollTo({
        top: 0,
      });
    },
    moveToBottom() {
      window.scrollTo(0, document.body.scrollHeight);
    },
  },
};
</script>

<style scoped>
.container {
  margin: 10px 60px;
  margin-bottom: 120px;
}
.thumbnail-container {
  background: #d9ebff;
  width: 100%;
  height: 488px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.thumbnail {
  /* width: 702px; */
  height: 458px;
  border-radius: 11px;
  border: #ebe3e3 0.5px solid;
}

.title-container {
  margin-top: 20px;
  display: flex;
  align-items: center;
}
.logo {
  width: 35px;
  height: 35px;
  margin-right: 10px;
}
.title {
  font-family: AppleSDGothicNeoSB;
  font-size: 26px;
}
.summary {
  font-family: AppleSDGothicNeoM;
  font-size: 16px;
  padding: 20px 5px;
}
.line::after {
  content: "";
  display: block;
  width: 100%; /* 너비를 조정할 수 있습니다 */
  height: 0.5px; /* 선의 두께를 조정할 수 있습니다 */
  background-color: #e3e6ea; /* 선의 색상을 설정합니다 */
}

.index-content-container {
  display: flex;
  position: relative;
}

.content-container {
  width: 82%;
  margin-top: 30px;
}

.content-container > div:not(:first-child) {
  margin-top: 60px;
}

.sub-title {
  font-family: AppleSDGothicNeoSB;
  font-size: 28px;
}
.sub-title::before {
  content: "";
  display: inline-block;
  width: 4px; /* 선의 너비를 설정합니다 */
  height: 28px; /* 높이를 조정할 수 있습니다 */
  background-color: #33b5e7; /* 선의 색상을 설정합니다 */
  margin-right: 13px; /* 원래 콘텐츠와 선 사이의 간격을 설정합니다 */
  vertical-align: middle;
}
.guide {
  font-family: AppleSDGothicNeoR;
  margin-top: 10px;
  margin-bottom: 25px;
  color: #aaacae;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
li {
  position: relative;
  padding-left: 20px; /* 커스텀 점과 텍스트 사이의 간격 */
  padding-bottom: 12px;
}
li::before {
  content: "•"; /* 점 모양의 문자 */
  position: absolute;
  left: 0;
  color: black; /* 점 색상 */
  font-size: 16px; /* 점 크기 */
}
.content {
  font-family: AppleSDGothicNeoM;
  margin-top: 15px;
  font-size: 16px;
}
.sub-content {
  margin-left: 30px;
  font-size: 16px;
}
.content-title {
  font-family: AppleSDGothicNeoSB;
  font-size: 19px;
  margin-bottom: 7px;
}
.domain-name {
  font-size: 19px;
  margin-right: 15px;
}
.tech-stack-list span:not(.domain-name) {
  background-color: #e9ecf2;
  border-radius: 5px;
  padding: 5px 9px;
  margin-right: 7px;
}
.feature {
  margin-bottom: 45px;
}
.feature-name {
  font-family: AppleSDGothicNeoSB;
  font-size: 17px;
}
.sub-feature {
  margin-left: 30px;
}
.gif {
  margin-top: 15px;
  width: 600px;
  border-radius: 11px;
  border: #ebe3e3 0.5px solid;
}
.description {
  font-family: AppleSDGothicNeoR;
  margin-top: 10px;
}

/* 위아래 이동 버튼 */
.move-control-btn {
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 1000;
}
.move-control-btn button {
  display: block;
  background-color: white;
  border-radius: 50%;
  font-size: 30px;
  width: 60px;
  aspect-ratio: 1/1;
  box-shadow: 0px 5px 5px 1px rgba(0, 0, 0, 0.068);
  border: none;
  color: #999;
  cursor: pointer;
}
.move-control-btn .up {
  margin-bottom: 20px;
}
</style>
