<template lang="ko">
	<div class="thumbnail-container">
		<div>
      <router-link :to="{name: `${routerName}`}">
        <img :src="require(`@/assets/${currentSrc}`)" class="thumbnail" @mouseover="changeToGif" @mouseout="changeToStatic">
      </router-link>
		</div>
		<div class="title-container">
			<img v-if="logoImg !== undefined" :src="require(`@/assets/${logoImg}`)" class="logo"><span class="title">{{title}}</span>
		</div>
    <div class="date">{{date}}</div>
	</div>
</template>

<script>
export default {
  data() {
    return {
      currentSrc: this.static,
    };
  },
  methods: {
    changeToGif() {
      this.currentSrc = this.animate;
    },
    changeToStatic() {
      this.currentSrc = this.static;
    },
  },
  props: ["animate", "static", "logoImg", "title", "routerName", "date"],
};
</script>

<style scoped>
.thumbnail-container {
  width: 629px;
}

.thumbnail {
  width: 629px;
  height: 338px;
  border-radius: 11px;
  border: #ebe3e3 0.5px solid;
  object-fit: contain;
  display: inline-block;
  margin-bottom: 10px;
  cursor: pointer;
  position: relative;
  padding: 10px;
}
.title-container {
  display: flex;
  align-items: center; /* 세로 중앙 정렬 */
}
.logo {
  width: 30px;
  height: 30px;
  margin-right: 5px;
}
.title {
  font-family: AppleSDGothicNeoSB;
  font-size: 20px;
}
.date {
  font-family: AppleSDGothicNeoR;
  opacity: 80%;
  font-size: 15px;
  margin-top: 10px;
}
</style>
