          title: "정식연재 / 도전만화",
          currentSrc: "challenge.png",
          static: "challenge.png",
          gif: "challenge.gif",
          descriptions: ["정식연재의 요일별 웹툰", "도전만화의 장르별 웹툰"],
        },
      ],
    };
