<template lang="ko">
	<div class="card-container">
		<img :src="require(`@/assets/${thumbnailName}`)" class="thumbnail">
		<div class="inner-content">
			<div class="title">{{title}}</div>
			<ul>
				<li v-for="detail in details">{{detail}}</li>
			</ul>
		</div>
	</div>
</template>

<script>
export default {
  props: ["thumbnailName", "title", "details"],
};
</script>

<style scoped>
.card-container {
  width: 244px;
  height: 287px;
  display: inline-block;
  margin-right: 45px;
  box-shadow: 3px 4px 14px -3px #ecf1f5;
  border-radius: 14px;
}
.thumbnail {
  width: 244px;
  height: 153px;
  object-fit: contain;
  background-color: #f6f8fa;
  border-top-left-radius: 14px;
  border-top-right-radius: 14px;
}
.inner-content {
  text-align: center;
}
.title {
  font-family: AppleSDGothicNeoM;
  margin-top: 15px;
  font-size: 18px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: AppleSDGothicNeoL;
  margin-top: 15px;
  font-size: 15px;
}
ul li {
  margin-bottom: 6px;
}
</style>
