<template lang="ko">
	<div class="index-container">
		<nav>
			<div class="index-title">Table of Contents</div>
			<ul>
				<li v-for="(title, index) in subTitles" :key="index" :class="{ active: activeIndex === index }">
          <a href="#" @click.prevent="scrollToTitle(index)">{{ title.text }}</a>
        </li>
			</ul>
		</nav>
	</div>
</template>

<script>
export default {
  data() {
    return {
      subTitles: [],
      activeIndex: 0, // 현재 활성화된 목차 인덱스
    };
  },
  mounted() {
    this.waitForImagesToLoad().then(() => {
      this.collectSubTitles();
    });
    window.addEventListener("scroll", this.onScroll); // 스크롤 이벤트 리스너 추가
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll); // 컴포넌트가 제거될 때 이벤트 리스너 제거
  },
  methods: {
    waitForImagesToLoad() {
      const images = document.querySelectorAll("img");
      const imagePromises = Array.from(images).map((img) => {
        return new Promise((resolve) => {
          img.onload = resolve;
          img.onerror = resolve; // 에러 발생 시에도 처리
        });
      });

      return Promise.all(imagePromises);
    },
    // 모든 sub-title을 수집
    collectSubTitles() {
      const subTitleElements = document.querySelectorAll(".sub-title");
      this.subTitles = Array.from(subTitleElements).map((element) => ({
        text: element.innerText,
        offsetTop: element.offsetTop + 600, //nav bar 크기만큼 아래로 이동
      }));
    },
    // 해당 섹션으로 스크롤
    scrollToTitle(index) {
      const targetOffset = this.subTitles[index].offsetTop;
      window.scrollTo({
        top: targetOffset,
        behavior: "smooth",
      });
    },
    onScroll() {
      // const scrollPosition = window.scrollY + window.innerHeight / 2; // 현재 스크롤 위치
      const scrollPosition = window.scrollY + 100;
      this.subTitles.forEach((title, index) => {
        if (scrollPosition >= title.offsetTop) {
          this.activeIndex = index; // 활성화된 목차 인덱스 업데이트
        }
      });
    },
  },
};
</script>

<style scoped>
.index-container {
  position: sticky;
  top: 150px;
  height: fit-content;
  padding: 30px 0;
}

.index-title {
  font-family: AppleSDGothicNeoB;
  font-size: 22px;
  margin-bottom: 10px;
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

nav ul li {
  margin-bottom: 10px;
  font: AppleSDGothicNeoM;
  font-size: 18px;
}

nav ul li a {
  text-decoration: none;
  color: gray;
}

nav ul li a:hover {
  text-decoration: underline;
}

ul li.active a {
  color: black; /* 활성화된 항목 색상 */
  font-weight: bold; /* 강조 표시 */
}
</style>
