<template lang="ko">
	<div class="container">
		<div class="main" ref="typedElement"></div>
	</div>
</template>

<script>
import Typed from "typed.js";

export default {
  mounted() {
    const options = {
      strings: ["back-end.web(developer)_ interested in UX"],
      typeSpeed: 50,
      backSpeed: 25,
      showCursor: true,
      cursorChar: " |",
      autoInsertCss: true,
      loop: true,
    };

    this.typed = new Typed(this.$refs.typedElement, options);
  },
  beforeDestroy() {
    // 메모리 누수를 방지하기 위해 컴포넌트가 소멸되기 전에 타이핑 인스턴스를 파괴합니다.
    this.typed.destroy();
  },
};
</script>

<style scoped>
.container {
  text-align: center;
  height: auto;
  margin-top: 210px;
}

.main {
  font-family: "DotGothic16", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 29px;
  display: inline-block;
}

/* Cursor */
::v-deep .typed-cursor {
  font-size: 24px;
  opacity: 60%;
}
</style>
