<template lang="ko">
	<div class="container">

		<div class="thumbnail-container">
			<img :src="require(`@/assets/${thumbnail.currentSrc}`)" class="thumbnail" @mouseover="changeToGif(thumbnail)" @mouseout="changeToStatic(thumbnail)">
		</div>
    <div class="guide">(상단 이미지 위에 마우스를 올리면 gif가 재생됩니다.)</div>

		<div class="title-container">
			<img :src="require(`@/assets/${logoImg}`)" class="logo"><span class="title">{{title}}</span>
		</div>
		<div class="summary">
			WebRTC를 활용한 화상회의와 자체 커뮤니티가 결합된 비대면 교육 플랫폼입니다.
		</div>
		<div class="line"></div>

    <div class="index-content-container">
      <div class="content-container">
        <!-- Overview -->
        <div class="block-container">
          <div class="sub-title">OverView</div>
          <ul class="content">
            <li>팀 구성: FE(3), BE(2), Server(1) - 총 6인</li>
            <li>역할: 기획 - 50% (아이디어 해커톤 우승), 디자인 - 50% (총 39개의 페이지 중 22개 페이지 디자인), BE(REST API) - 70%, README 작성 - 100%</li>
            <li>제작 기간: 2022.07.11 ~ 2022.08.19</li>
          </ul>
        </div>

        <!-- Project Goals -->
        <div class="block-container">
          <div class="sub-title">Project Goals</div>
          <div class="content">
            코로나 19로 인해 비대면 수업이 증가함에 따라, 학습에 있어 교수자와 학습자 사이의 소통 채널이 가장 필요하다는 응답 결과를 토대로 제작하였습니다. </br>
            또한, 여러 서비스(Mattermost, Webex, SSAFY 포털 사이트)를 이용하며 겪은 불편함은 해소하고 장점은 통합된 WebRTC를 활용한 화상회의와 커뮤니티가 결합된 </br>
            비대면 교육 플랫폼입니다. 
          </div>
        </div>

        <!-- Teck Stack -->
        <div class="block-container">
          <div class="sub-title">Tech Stack</div>
          <ul class="content tech-stack-list">
            <li><span class="domain-name">FE</span> <span>React</span> <span>Redux</span> <span>TypeScript</span> <span>WebRTC</span> <span>OpenVidu</span> <span>Style-components</span></li>
            <li><span class="domain-name">BE</span> <span>SpringBoot</span> <span>Hibernate</span> <span>Gradle</span> <span>Mysql</span> <span>JWT</span></li>
            <li><span class="domain-name">Server</span> <span>EC2</span> <span>nginx</span> <span>Ubuntu</span> <span>Dokcer</span>
            <li><span class="domain-name">Tool</span> <span>Eclipse</span> <span>VSCode</span> <span>Figma</span> <span>Gitlab</span> <span>Jira</span></li>
          </ul>
        </div>

        <!-- Features -->
        <div class="block-container">
          <div class="sub-title">Features✨</div>
          <div class="guide">(구현을 담당한 주요 기능들을 간추려 작성하였습니다. 이미지 위에 마우스를 올리면 gif가 재생됩니다.)</div>
          <div>
            <div v-for="feature in features" class="feature">
              <div class="feature-name">{{feature.title}}</div>
              <img :src="require(`@/assets/atti/${feature.currentSrc}`)" class="gif" @mouseover="changeToGif(feature)" @mouseout="changeToStatic(feature)">
              <ul class="description">
                <li v-for="description in feature.descriptions">{{description}}</li>
              </ul>
            </div>

            <div class="feature-name">그 외 기능은 Github에 세부적으로 작성되어 있습니다.</div>
          </div>
        </div>

        <!-- Github --->
        <div class="block-container">
          <div class="sub-title"><a href="https://github.com/joohee56/ATTI" class="link" target="blank">Github</a><img src="@/assets/arrow.png"></div>
        </div>
      </div>

      <IndexComp></IndexComp>
    </div>

    <!-- 위아래 이동 버튼 -->
    <div class="move-control-btn">
      <button class="up" @click="moveToTop">▴</button>
      <button @click="moveToBottom">▾</button>
    </div>
	</div>
</template>

<script>
import IndexComp from "./IndexComp.vue";

export default {
  data() {
    return {
      thumbnail: {
        currentSrc: "thumbnail_atti.png",
        static: "thumbnail_atti.png",
        gif: "intro_atti.gif",
      },
      logoImg: "logo_atti.png",
      title: "ATTI",
      features: [
        {
          title: "문자 인증",
          currentSrc: "messageAuthentication.jpg",
          static: "messageAuthentication.jpg",
          gif: "messageAuthentication.gif",
          descriptions: ["Naver SENS 사용"],
        },
        {
          title: "카카오 로그인",
          currentSrc: "kakaoLogin.png",
          static: "kakaoLogin.png",
          gif: "kakaoLogin.gif",
          descriptions: [
            "카카오 API를 활용하여 기존 방식의 회원가입보다 접근성이 좋고 편리한 방법 제공",
          ],
        },
        {
          title: "수업 생성 (관리자)",
          currentSrc: "createTimetable.png",
          static: "createTimetable.png",
          gif: "createTimetable.gif",
          descriptions: [
            "시간표의 빈 공간 클릭하여 수업 생성",
            "해당 채널에 가입된 학생들 한정으로 시간표와 수업 입장 버튼 노출",
          ],
        },
        {
          title: "수업 입장",
          currentSrc: "classroomEntry.png",
          static: "classroomEntry.png",
          gif: "classroomEntry.gif",
          descriptions: [
            "수업 클릭 시 openvidu와 연동하여 수업실 입장",
            "입장과 함께 자동 출석체크",
          ],
        },
        {
          title: "접속자/미접속자 표시",
          currentSrc: "attendanceCheck.png",
          static: "attendanceCheck.png",
          gif: "attendanceCheckZoom.png",
          descriptions: ["일일이 미접속자를 체크할 필요없이 한 눈에 확인 가능"],
        },
        {
          title: "질문게시판 연동",
          currentSrc: "questionBoard.png",
          static: "questionBoard.png",
          gif: "questionBoard.gif",
          descriptions: [
            "수업 도중 올라온 질문들은 수업 종료 후 자동으로 질문 게시판에 업로드",
            "질문과 질문에 관한 답변 확인 가능",
          ],
        },
        {
          title: "출결변경 (관리자)",
          currentSrc: "attendanceChange.png",
          static: "attendanceChange.png",
          gif: "attendanceChange.gif",
          descriptions: ["각 수업 당 학생 출결 상황 변경"],
        },
      ],
    };
  },
  components: { IndexComp },
  methods: {
    changeToGif(img) {
      img.currentSrc = img.gif;
    },
    changeToStatic(img) {
      img.currentSrc = img.static;
    },
    moveToTop() {
      window.scrollTo({
        top: 0,
      });
    },
    moveToBottom() {
      window.scrollTo(0, document.body.scrollHeight);
    },
  },
};
</script>

<style scoped>
