  margin: 10px 60px;
  margin-bottom: 120px;
}
.thumbnail-container {
  background: linear-gradient(45deg, #adc6f6, #deadd8);
  width: 100%;
  height: 488px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.thumbnail {
  /* width: 702px; */
  height: 458px;
  border-radius: 11px;
  border: #ebe3e3 0.5px solid;
}
.title-container {
  margin-top: 20px;
  display: flex;
  align-items: center;
}
.logo {
  width: 35px;
  height: 35px;
  margin-right: 10px;
}
