}

.sub-title {
  font-family: AppleSDGothicNeoSB;
  font-size: 28px;
}
.sub-title::before {
  content: "";
  display: inline-block;
  width: 5px; /* 선의 너비를 설정합니다 */
  height: 28px; /* 높이를 조정할 수 있습니다 */
  background-color: #adc6f6; /* 선의 색상을 설정합니다 */
  margin-right: 13px; /* 원래 콘텐츠와 선 사이의 간격을 설정합니다 */
  vertical-align: middle;
}
