  content: "•"; /* 점 모양의 문자 */
  position: absolute;
  left: 0;
  color: black; /* 점 색상 */
  font-size: 16px; /* 점 크기 */
}
ul > :last-child {
  margin-bottom: 15px;
}
.content {
  font-family: AppleSDGothicNeoM;
  margin-top: 15px;
  font-size: 16px;
}
.sub-content {
  margin-left: 30px;
  font-size: 16px;
}
.content-title {
  font-family: AppleSDGothicNeoSB;
  font-size: 19px;
  margin-bottom: 7px;
}
.domain-name {
  font-size: 19px;
  margin-right: 15px;
}
.tech-stack-list span:not(.domain-name) {
  background-color: #e9ecf2;
