  padding: 5px 9px;
  margin-right: 7px;
}
.feature {
  margin-bottom: 45px;
}
.feature-name {
  font-family: AppleSDGothicNeoSB;
  font-size: 17px;
}
.gif {
  margin-top: 15px;
  width: 600px;
  border-radius: 11px;
  border: #ebe3e3 0.5px solid;
}
.description {
  font-family: AppleSDGothicNeoR;
  margin-top: 10px;
}
/* 위아래 이동 버튼 */
.move-control-btn {
  position: fixed;
  right: 50px;
